// COMMON :: START

@import "macros/lang";
@import "macros/category";
@import "macros/sort";

.copyright {
	display: flex;
	align-items: center;
	justify-content: center;

	&__text {
		text-align: center;
		font-size: 16px;
		color: rgb(135, 144, 176);
		line-height: 2.273;
		@include adaptive(767) {
			font-size: 12px;
			line-height: 2;

			span {
				font-size: 0;
				display: block;
			}
		}
	}
}

// COMMON :: END
