///
/// ADDITIONALLY
/// ===============
$maxWidth: 1630px;
$paddingLeft: 20px;
$paddingRight: $paddingLeft;

%grid {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding-right: $paddingRight;
  padding-left: $paddingLeft;
}


///
/// MAIN
/// ===============
.c-grid {
  @extend %grid;
  //@if($maxWidth) {
  //  max-width: $maxWidth;
  //}
	max-width: 100%;
	padding-left: 145px;
	padding-right: 145px;
	@include adaptive(1919) {
		max-width: 1630px;
		padding-left: 20px;
		padding-right: 20px;
	}
	@include adaptive(767) {
		padding-left: 10px;
		padding-right: 10px;
	}

  &-fluid {
    @extend %grid;
    max-width: 100%;
  }
}

